.navbar__links ul {
  list-style-type: none;
  display: flex;
  align-items: center;
}
.search a {
  text-decoration: none;
}
.search {
  display: flex;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  color: var(--bg-primary);
  justify-content: space-evenly;
  background: var(--text-secondary);
  align-items: center;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.12);
}

.logo {
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
}

.otherBtns {
  color: var(--bg-primary);
  font-size: 1.2rem;
  font-family: Arial, Helvetica, sans-serif;
  padding: 11px;
  background: transparent;
  outline: none;
  border: none;
  font-weight: normal;
}
.otherBtns:hover {
  color: var(--bg-secondary);
}
