.modal {
  background: #fefefe;
  margin: 0 auto;
  width: 90%;
  max-width: 400px;
  padding: 2em;
  border-radius: 20px;
  box-shadow: 1px 1px 10px var(--bg-primary-transparent);
}
.modal .form-control label {
  font-weight: 400;
}
.modal__header {
  text-align: center;
  font-weight: 400;
  font-size: 1.2em;
  text-transform: capitalize;
}
.modal__header h2 {
  margin: 1.2em;
}
.modal__body .form-control {
  width: 100%;
  margin: 1em 0.1em;
}
.modal__body .btn,
.modal__body textarea,
.modal__body .btn-light,
.modal__body input[type='text'],
.modal__body input[type='submit'],
/* .modal__body select, */
.modal__body input[type='password'] {
  width: 100%;
  font-size: 1em;
  padding: 1em;
  margin: 0.4em 0;
  border-radius: 2px;
  border: 2px solid var(--text-secondary);
}
.modal__footer {
  text-align: center;
  margin-top: 3em;
}
.modal .btn {
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}
.modal .btn-light {
  text-transform: capitalize;
}
.modal__body input:focus {
  border-color: var(--bg-primary);
}
.modal__body select {
  display: block;
  font-size: 16px;
  line-height: 1.3;
  padding: 0.6em 1.4em 0.5em 0.8em;
  width: 100%;
  max-width: 100%;
  border: 1px solid var(--bg-primary-transparent);
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  border-radius: 2px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
  background-repeat: no-repeat, repeat;
  background-position: right 0.7em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.modal__body select::-ms-expand {
  display: none;
}
.modal__body select:focus {
  border-color: var(--bg-primary);
}
.modal__body select option {
  font-weight: normal;
}
