* {
  font-family: 'Oswald', sans-serif;
  font-weight: 200;
  box-sizing: border-box;
}
:root {
  --bg-primary: #272a5a;
  --bg-primary-transparent: #272a5a70;
  --bg-secondary: #545cd4;
  --text-primary: #1c1c34;
  --text-secondary: #e2e4ee;
}
body {
  background: var(--bg-primary);
  position: relative;
}
body::before {
  position: fixed;
  content: '';
  top: 8em;
  left: 25em;
  height: 30em;
  width: 30em;
  background: var(--bg-secondary);
  border-radius: 41% 57% 36% 42% / 63% 43% 12% 46%;
  z-index: -1;
}
body::after {
  position: fixed;
  content: '';
  top: 10em;
  right: 30em;
  height: 25em;
  width: 25em;
  background: var(--bg-secondary);
  border-radius: 43% 53% 36% 42% / 23% 55% 46% 22%;
  z-index: -1;
}
.btn {
  color: #fff;
  background: var(--bg-primary);
}
.btn-light {
  color: var(--bg-primary-transparent);
  background: var(--text-secondary);
}
.App {
  margin-top: 2rem;
}
