.news-list {
  margin: 6em 3em;
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}
.news-post {
  background: #fefefe;
  padding: 2em;
  border-radius: 20px;
  box-shadow: 1px 1px 10px var(--bg-primary-transparent);
}
.news-post .form-control label {
  font-weight: 400;
}
.news-post__header {
  font-weight: 400;
  font-size: 1.2em;
  text-transform: capitalize;
}
.news-post__header img {
  width: 100%;
}
.news-post__body .form-control {
  width: 100%;
  margin: 1em 0.1em;
}
.news-post__body .btn,
.news-post__body .btn-light,
.news-post__body input[type='text'],
.news-post__body input[type='submit'],
.news-post__body input[type='password'] {
  width: 100%;
  font-size: 1em;
  padding: 1em;
  margin: 0.4em 0;
  border-radius: 10px;
  border: 2px solid var(--text-secondary);
}
.news-post__footer {
  margin-top: 2em;
}
.news-post .btn {
  text-transform: uppercase;
}
.news-post .btn-light {
  text-transform: capitalize;
}
.news-post__body input:focus {
  border-color: var(--bg-primary);
}
